.services {
    padding: 0rem 3rem 0rem 3rem;
    display: flex;
    height: 100vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
}
.awesome{
    flex: 1;
    display: flex;
    flex-direction: column;
    position: relative;
}
.awesome>span:nth-child(1){
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--black);
}
.awesome>span:nth-child(2){
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--orange);
}
.awesome>span:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}
.s-button{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}
.cards{
    flex: 1;
    position: relative;
}
.cards>*{
    position: absolute;
}

.s-blur1{
    top: 13rem;
    left: -18rem;
}
.s-blur2{
    left: 14rem;
    top: 8rem;
}
@media screen and (max-width: 480px) {
    .services{
        position: relative;
        margin-top: 15rem;
        height: 66rem;
        flex-direction: column;
        padding: 0;
        gap: 5rem;
    }
    .cards{
        display: flex;
        flex-direction: column;
        gap: 17rem;
    }
    .cards> * {
        position: static;
    }
}