.footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;
    gap: 0.5rem;
}
.name{
    margin-top: 5rem;
    text-transform:uppercase ;
    font-size: 2rem;
}
.f-content{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 4rem;
    gap: 4rem;
    color: white;
}
.copy{
    margin-bottom: 2rem;
}
@media screen and (max-width: 480px) {
    .footer{
        height: 40vh;
    }
    .footer img{
        height: 100%;
    }
    .f-content{
        gap: 0.5rem;
    }
    .name{
        margin-top: 3.5rem;
    }
    .copy{
        font-size: 15px;
    }
    .f-icons{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        width: 100%;
    }
}