.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;

}
.n-left{
    flex: 1;
    display: flex;
    align-items: center;
    gap: 2rem;
}
.n-name{
    font-size: 1.3rem;
    font-weight: bolder;
}
.n-name span{
    font-size: 1.3rem;
    font-weight: bolder;
}

.n-right{
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    font-weight: normal;

}
.n-list {
    flex: 10;
}

.n-list>ul {
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    cursor: pointer;
}

.n-list>ul>li:hover{
    cursor: pointer;
    color: var(--orange);
}
.n-button{
    flex: 2;
}
.nav-button{
    display: none;
}
@media screen and (max-width: 480px) {
    .n-list{
        display: none;
    }
    .hide{
        display: none;
    }
    .n-button{
        display: none;
    }
    .nav-button{
        display: block;
        margin-right: -5rem;
    }
}