.about{
    display: flex;
    height: 100vh;
    margin-top: 3rem;
    position: relative;
}
h1{
    font-weight: bold;
    font-size: 3rem;
    color: var(--orange);
}
h2{
    font-weight: bold;
    font-size: 1rem;
    color: var(--black);
}
h2 span{
    color: var(--orange);
}
p{
    color: var(--gray);
}
.a-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    justify-content: center;
}
.list{
    display: flex;
    margin-left: -10px;
}
.list ul li{
    list-style: none;
    line-height: 50px;
    font-weight: bold;
    border-bottom: 1px solid var(--orange);
}
.list ul li span{
    color: var(--orange);
    font-size: 15px;
}
.a-right{
    flex: 1;
}
.a-right img{
    width: 400px;
    border-radius: 50%;
    border-bottom: 5px solid var(--orange);
    border-top: 5px solid var(--orange);
    position: absolute;
    top: 25%;
    left: 60%;
}
@media screen and (max-width: 480px) {
    .about{
        margin-top: 0;
        display: flex;
        flex-direction: column-reverse;
        
    }
    .a-left{
        top: 15rem;
    }
    .a-right img{
        width: 400px;
        border-radius: 50%;
        border-bottom: 5px solid var(--orange);
        border-top: 5px solid var(--orange);
        position: absolute;
        top: -9rem;
        left: -0.6rem !important;
    }
    .list{
        margin-left: -49px;
    }
}