.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-left{
    display: flex;
    flex: 1;
    flex-direction: column;
    position: relative;
    gap: 2rem;
}
.i-name{
    display: flex;
    flex-direction: column;
}
.i-name > span:nth-child(1){
    font-size: 3rem;
    font-weight: bolder;
    color: var(--black);
}
.i-name > span:nth-child(2){
    font-size: 3rem;
    font-weight: bolder;
    color: var(--orange);
}
.i-name > span:nth-child(3){
    font-size: 14px;
    font-weight: normal;
    color: var(--gray);
    margin-top: 10px;
}
.i-button{
    width: 6rem;
    height: 2rem;
}
.i-icons{
    display: flex;
    margin-top: 3rem;
    gap: 0rem;
}
.i-icons > *{
    transform: scale(0.5);
}
.i-icons > *:hover{
    cursor: pointer;
}
.i-right{
    flex: 1;
    position: relative;
}
.i-right> * {
    position: absolute;
    z-index: 1;
}
.i-right>:nth-child(1){
    transform: scale(0.69);
    left: -15%;
    top: -9%;
}
.i-right>:nth-child(2){
    transform: scale(0.67);
    left: -3rem;
    top: -4.6rem;
}
.i-right>:nth-child(3){
    transform: scale(1);
    left: 28%;
    top: 15%;
    border-radius: 20px;
}
.i-right>:nth-child(4){
    transform: scale(0.6);
    border-radius: 50%;
    top: -19%;
    left: -24%;
    padding: 0;
}
.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(72px);
}

@media screen and (max-width: 480px) {
    .intro{
        flex-direction: column;
        height: 64rem;
    }
    .i-left{
        gap: 1rem;
    }
    .i-right{
        transform: scale(0.8);
        left: -3rem;
    }
    .i-right .blur{
        display: none;
    }
    .floating:nth-of-type(1){
        top: -7rem !important;
    }
    .i-icons{
        margin-top: 1rem;
    }
}